.footer {
  width: 100%;
  position: relative;
  padding: 2rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;

  .videoDiv {
    position: absolute;
    height: 100%;
    width: 100%;

    video {
      height: 100%;
      object-fit: cover;
    }
    &::after {
      content: "";
      position: absolute;
      background: rgba(10, 84, 107, 0.307);
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      mix-blend-mode: multiply;
    }
  }
  .secContent {
    width: 100%;
    height: max-content;
    padding: 2rem initial;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: auto;
    row-gap: 2rem;
    z-index: 100;

    .contactDiv {
      justify-content: space-between;
      flex-direction: column;
      gap: 1rem;

      .text {
        small {
          font-size: 13px;
          font-weight: 400;
          color: var(--WhiteColor);
        }
        h2 {
          color: var(--WhiteColor);
          font-size: 1.8rem;
        }
      }
      .inputDiv {
        width: 100%;
        gap: 1rem;
        flex-direction: column;

        input {
          width: 100%;
          padding: 0.5rem 0.6rem;
          border-radius: 3rem;
          border: 1px solid var(--WhiteColor);
          outline: none;
          background: transparent;
          color: var(--WhiteColor);

          &::placeholder {
            color: var(--WhiteColor);
            opacity: 0.5;
          }
        }
        .btn {
          width: 100%;
          justify-content: center;
          color: var(--WhiteColor);
          font-weight: 500;
          gap: 0.5rem;

          .icon {
            font-size: 18px;
          }
        }
      }
    }
    .footerCard {
      position: relative;
      padding: 1rem 1rem 4rem;
      gap: 2rem;
      border-radius: 1rem;
      background: var(--bodyColor);
      width: 100%;
      justify-content: center;
      flex-direction: column;
      align-items: flex-start;
      overflow: hidden;

      .footerIntro {
        flex-basis: 50%;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        row-gap: 0.5rem;

        .logoDiv {
          .logo {
            color: var(--blackColor);
            font-weight: 600;

            .icon {
              font-size: 25px;
              color: var(--PrimaryColor);
              margin-right: 10px;
            }
          }
        }

        .footerParagraph {
          font-size: 13px;
          color: var(--textColor);
        }
        .footerSocials {
          column-gap: 0.5rem;

          .icon {
            color: var(--blackColor);
            font-size: 20px;
            &:hover {
              color: var(--PrimaryColor);
            }
          }
        }
      }
      .footerLinks {
        flex-basis: 50%;
        width: 100%;
        gap: 1rem;
        grid-template-columns: repeat(2, 1fr);
        justify-content: space-between;

        .linkGroup {
          .groupTitle {
            display: block;
            font-weight: 600;
            color: var(--blackColor);
            margin-bottom: 0.5rem;
          }
          .footerList {
            font-size: 15px;
            color: var(--textColor);
            transition: 0.3s ease-in-out;

            .icon {
              font-size: 15px;
              color: var(--greyText);
            }
            &:hover {
              color: var(--PrimaryColor);
              transform: translateX(7px);
              cursor: pointer;

              .icon {
                color: var(--PrimaryColor);
              }
            }
          }
        }
      }
      .footerDiv {
        flex-direction: column;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        background: var(--PrimaryColor);
        padding: 0.5rem 2rem;

        small {
          font-size: 10px;
          color: var(--WhiteColor);
        }
      }
    }
  }
  .scroll-to-top {
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to right, #0172af, #74febd) !important;
    &:hover {
      transform: translateY(-5px);
    }
    svg {
      fill: var(--WhiteColor) !important;
    }
  }
}

@media screen and (min-width: 500px) {
  .footer {
    .secContent {
      .footerCard {
        .footerLinks {
          grid-template-columns: repeat(3, 1fr);
          padding-bottom: 1rem;
        }
        .footerDiv {
          justify-content: space-between;
          flex-direction: row;

          small {
            font-size: 14px;
            color: var(--WhiteColor);
          }
        }
      }
    }
  }
}
@media screen and (min-width: 660px) {
  .footer {
    .secContent {
      .contactDiv {
        flex-direction: row;
        justify-content: space-between;

        .inputDiv {
          width: max-content;
          flex-direction: row;
          justify-content: flex-start;

          input {
            width: 60%;
          }
          .btn {
            width: max-content;
          }
        }
      }
    }
  }
}
@media screen and (min-width: 840px) {
  .footer {
    padding: 5rem 0;
    .secContent {
      .footerCard {
        flex-direction: row;

        .footerLinks {
          grid-template-columns: repeat(3, 1fr);
          padding-bottom: 1rem;
        }
      }
    }
  }
}
@media screen and (min-width: 1024px) {
  .footer {
    .secContent {
      .footerCard {
        .footerIntro {
          .footerParagraph {
            font-size: 15px;
            color: var(--textColor);
          }

          .footerSocials {
            column-gap: 0.5rem;

            .icon {
              color: var(--textColor);
              font-size: 25px;

              &:hover {
                color: var(--PrimaryColor);
              }
            }
          }
          .footerLinks {
            .linkGroup {
              .footerList {
                font-size: 16px;
                padding: 5px 0;
              }
            }
          }
        }
      }
    }
  }
}
