.main {
  width: 100%;

  .secTitle {
    .title {
      position: relative;
      width: max-content;
      color: var(--textColor);
      margin: 1rem 0;
      z-index: 2;

      &::after {
        position: absolute;
        content: "";
        background: var(--SecondaryColor);
        height: 5px;
        width: 120px;
        right: 0;
        bottom: 5px;
        z-index: -1;
      }
    }
  }
  .secContent {
    justify-content: center;
    gap: 1.5rem;

    .singleDestination {
      height: 100%;
      display: grid;
      row-gap: 10px;
      border-radius: 10px;
      align-items: center;
      background: var(--cardBG);
      box-shadow: 0 2px 4px rgba(140, 140, 141, 0.549);
      overflow: hidden;
      &:hover {
        background: var(--cardHover);
        box-shadow: 1 4px 4px rgba(85, 85, 114, 0.549);
        transition: 0.3s ease;
      }
      .imageDiv {
        height: 180px;
        width: 100%;
        overflow: hidden;

        img {
          height: 100%;
          width: 100%;
          object-fit: cover;
          transition: 0.5s ease-in-out;
          -webkit-transition: 0.5s ease-in-out;
          overflow: hidden;
        }
        &:hover img {
          transform: scale(1.2);
        }
      }
    }
    .cardInfo {
      padding: 1rem;

      .desTitle {
        color: var(--blackColor);
        font-size: 1.25rem;
      }
      .continent {
        gap: 0.5rem;
        .icon {
          color: var(--textColor);
          font-size: 18px;
        }
        .name {
          color: var(--textColor);
          font-size: 14px;
          font-weight: 400;
        }
      }
      .fees {
        width: 100%;
        justify-content: space-between;
        padding: 1rem;
        margin: 1rem 0;
        border-top: 1.5px solid var(--greyText);
        border-bottom: 1.5px solid var(--greyText);

        .price {
          color: var(--blackColor);
          max-width: 100px;
          line-height: 20px;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 1.5rem;
        }
      }
      .desc {
        font-size: 13.5px;
        color: var(--textColor);
        font-weight: 600;
      }
      .btn {
        margin-top: 1rem;
        color: var(--WhiteColor);
        font-weight: 600;
        justify-content: space-between;
        gap: 0.5rem;

        .icon {
          align-self: center;
          font-size: 18px;
        }
        &:hover .icon {
          transition: 0.5s ease-in-out;
          transform: translateX(6px);
        }
      }
    }
  }
}

@media screen and (min-width: 500px) {
  .title {
    font-size: 1.5rem;
    &::after {
      width: 155px !important;
    }
  }
  .secContent {
    grid-template-columns: repeat(2, 1fr);

    .singleDestination {
      height: auto;
    }
  }
}

@media screen and (min-width: 840px) {
  .secContent {
    grid-template-columns: repeat(3, 1fr);

    .singleDestination {
      height: auto;
    }
  }
}
